<template>
	<div class="SelectOrder">
		<header><i class="iconfont icon-xiangzuo1" @click="toCycleCount"></i><span></span></header>
		<div class="container">

			<table cellspacing="0">
				<thead>
					<tr>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
						<td></td>
					</tr>
				</thead>
				<tbody>
					<tr v-for='(dataItem,index) in CycleCount_RFCountSel'  :id="index"  :key='index'  @click="selectData(index)">
						<td class="iconfont"  :class="{ 'icon-xuanzhong':index==detail_item,'icon-weixuanzhong':index!=detail_item}"></td>
						<td>{{dataItem.CountNo}}</td>
						<td>{{dataItem.CountType}}</td>
						<td>{{dataItem.AddDate}}</td>
						<td>{{dataItem.AddWho}}</td>
						<td>{{dataItem.CountStatus}}</td>
					</tr>
				</tbody>
			</table>

			<div class="kong"></div>
			<p class="positionFixed"><span class="g_singleBtn" @click="selectData_submit"></span></p>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"SelectOrder",
		data(){
			return{
				CycleCount_RFCountSel:[],//
				detail_item:0,//
			}
		},
		created(){
			console.log(this.$store.state.CycleCount_RFCountSel)

			var CycleCount_RFCountSel = this.$store.state.CycleCount_RFCountSel;

			this.CycleCount_RFCountSel = CycleCount_RFCountSel;
		},
		methods:{
			//  
			toCycleCount:function(){

				this.$router.push({
					name:"CycleCount",
				})
			},
			//
			selectData:function(idx){

				this.detail_item = idx;

				
			},
			//  
			selectData_submit:function(){


				var CountNo = this.CycleCount_RFCountSel[this.detail_item].CountNo       //
				var CountType =  this.CycleCount_RFCountSel[this.detail_item].CountType  //

				this.$router.push({
					name:"CycleCount",
					query:{
						SelectOrder:{
							CountNo:CountNo,
							CountType:CountType
						}
					}
				})
			}
		}
	}
</script>
<style lang="less" scoped>
	.SelectOrder{
		&>.container{
			width:100%;
			font-size: 30/50rem;
			padding-bottom: 130rpx;
			table{
				tr{
					td:nth-child(2){
						min-width: 150/50rem;
					}
					td:nth-child(3){
						min-width: 150/50rem;
					}
					td:nth-child(4){
						min-width: 320/50rem;
					}
					td:nth-child(5){
						min-width: 120/50rem;
					}
					td:nth-child(6){
						min-width: 120/50rem;
					}
				}
			}
		}
	}
</style>


