<template>
	<div class="TakeDelivery">
		<header><i class="iconfont icon-xiangzuo1" @click="ToReceipt"></i><span></span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div>
				<div>
					<span></span>
					<input    class="readonly_input" readonly  v-model="input_ReceiptNum"></input>
				</div>
				<div>
					<span></span>
					<input    class="readonly_input"  readonly  v-model="input_CustomerNum"></input>
				</div>
			</div>
			<div>
				<div>
					<span></span>
					<input  class="readonly_input"  readonly    v-model="input_CommodityCode"></input>
				</div>
				<div>
					<span></span>
					<input    class="readonly_input"  readonly  v-model="input_CustomerCode"></input>
				</div>
			</div>
			<div class="goodsName">
				<span></span>
				<textarea  class="readonly_input" v-model="input_CommodityName"   readonly></textarea>
			</div>
			<div>
				<div>
					<span></span>
					<input    class="readonly_input"  readonly  v-model="input_toBeReceived"></input>
				</div>
				<div>
					<span></span>
					<input    class="readonly_input"   readonly  v-model="input_AmountReceived"></input>
				</div>
			</div>
			<div>
				<div>
					<!-- @keyup.enter="getFocus('input_batchNum')" -->
					<span></span>
					<input type="number"   requireds="input_num" @blur="onblur(true,'input_num')"   ref="input_num" @keyup.enter="nextFocus('input_num')"   @focus="g_focus('input_num')"  placeholder=""   v-model="input_num"></input>
					<i v-if="input_num" @click="clearInput('input_num')"  class="iconfont icon-cuowu"></i>
				</div>
				<div>
					<span></span>
					<input    class="readonly_input"  readonly  v-model="input_unit"></input>
				</div>
			</div>
			<div class="select"  v-if="parseInt(SummaryData.ShowLottable02OnRFReceipt)">
				<span></span>
				<select  ref="ref_StateOfGoods" value = "">
					<option v-for='(dataItem,index) in StateOfGoodsData'  :id="index"  :key='index' >{{dataItem.Descr}}</option>
				</select>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable03OnRFReceipt)" >
				<span></span>
				<input ref="input_batchNum"  copyRef="input_batchNum"  requireds="Lottable03OnRFReceiptMandatory"  @blur="onblur(parseInt(SummaryData.Lottable03OnRFReceiptMandatory),'Lottable03OnRFReceiptMandatory')"  @keyup.enter="nextFocus('Lottable03OnRFReceiptMandatory')"     style="text-transform:none;"   @focus="g_focus('input_batchNum')"  v-model="input_batchNum"></input>
				<i v-if="input_batchNum" @click="clearInput('input_batchNum')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable04OnRFReceipt)">
				<span></span>
				<input   ref="input_DateofManufacture" copyRef="input_DateofManufacture"  requireds="Lottable04OnRFReceiptMandatory" @blur="onblur(parseInt(SummaryData.Lottable04OnRFReceiptMandatory),'Lottable04OnRFReceiptMandatory')"   @keyup.enter="nextFocus('Lottable04OnRFReceiptMandatory')"  @focus="g_focus('input_DateofManufacture')"     v-model="input_DateofManufacture"></input>
				<i v-if="input_DateofManufacture" @click="clearInput('input_DateofManufacture')"  class="iconfont icon-cuowu"></i>
			</div>
			<div  v-if="parseInt(SummaryData.ShowLottable05OnRFReceipt)">
				<span></span>
				<input   ref="input_ExpirationDate"  copyRef="input_ExpirationDate"   requireds="Lottable05OnRFReceiptMandatory" @blur="onblur(parseInt(SummaryData.Lottable05OnRFReceiptMandatory),'Lottable05OnRFReceiptMandatory')"  @keyup.enter="nextFocus('Lottable05OnRFReceiptMandatory')"   @focus="g_focus('input_ExpirationDate')"  v-model="input_ExpirationDate"></input>
				<i v-if="input_ExpirationDate" @click="clearInput('input_ExpirationDate')"  class="iconfont icon-cuowu"></i>
			</div>
			<div  v-if="parseInt(SummaryData.ShowLottable06OnRFReceipt)">
				<span>06</span>
				<input   ref="Lottable06"  copyRef="Lottable06"     requireds="Lottable06OnRFReceiptMandatory"  @blur="onblur(parseInt(SummaryData.Lottable06OnRFReceiptMandatory),'Lottable06OnRFReceiptMandatory')"    @focus="g_focus('Lottable06')"   @keyup.enter="nextFocus('Lottable06OnRFReceiptMandatory')"   v-model="Lottable06"></input>
				<i v-if="Lottable06" @click="clearInput('Lottable06')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable07OnRFReceipt)">
				<span>07</span>
				<input   ref="Lottable07" copyRef="Lottable07"   @blur="onblur(parseInt(SummaryData.Lottable07OnRFReceiptMandatory),'Lottable07OnRFReceiptMandatory')"  requireds="Lottable07OnRFReceiptMandatory"   @focus="g_focus('Lottable07')"   @keyup.enter="nextFocus('Lottable07OnRFReceiptMandatory')"    v-model="Lottable07"></input>
				<i v-if="Lottable07" @click="clearInput('Lottable07')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable08OnRFReceipt)">
				<span>08</span>
				<input   ref="Lottable08"  copyRef="Lottable08"  @blur="onblur(parseInt(SummaryData.Lottable08OnRFReceiptMandatory),'Lottable08OnRFReceiptMandatory')"  requireds="Lottable08OnRFReceiptMandatory"  @focus="g_focus('Lottable08')"   @keyup.enter="nextFocus('Lottable08OnRFReceiptMandatory')"    v-model="Lottable08"></input>
				<i v-if="Lottable08" @click="clearInput('Lottable08')"  class="iconfont icon-cuowu"></i>
			</div>
			<div  v-if="parseInt(SummaryData.ShowLottable01OnRFReceipt)">
				<span>01</span>
				<input   ref="Lottable01"   copyRef="Lottable01"  @blur="onblur(parseInt(SummaryData.Lottable01OnRFReceiptMandatory),'Lottable01OnRFReceiptMandatory')"  requireds="Lottable01OnRFReceiptMandatory"  @focus="g_focus('Lottable01')"  @keyup.enter="nextFocus('Lottable01OnRFReceiptMandatory')"    v-model="Lottable01"></input>
				<i v-if="Lottable01" @click="clearInput('Lottable01')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable09OnRFReceipt)">
				<span></span>
				<input   ref="input_ReceiptDate"   copyRef="input_ReceiptDate"   @blur="onblur(parseInt(SummaryData.Lottable09OnRFReceiptMandatory),'Lottable09OnRFReceiptMandatory')"  requireds="Lottable09OnRFReceiptMandatory"   @focus="g_focus('input_ReceiptDate')"   @keyup.enter="nextFocus('Lottable09OnRFReceiptMandatory')"    v-model="input_ReceiptDate"></input>
				<i v-if="input_ReceiptDate" @click="clearInput('input_ReceiptDate')"  class="iconfont icon-cuowu"></i>
			</div>
			<div v-if="parseInt(SummaryData.ShowLottable10OnRFReceipt)">
				<span>10</span>
				<input   ref="Lottable10"    copyRef="Lottable10"  @blur="onblur(parseInt(SummaryData.Lottable10OnRFReceiptMandatory),'Lottable10OnRFReceiptMandatory')"  requireds="Lottable10OnRFReceiptMandatory"  @focus="g_focus('Lottable10')"  @keyup.enter="nextFocus('Lottable10OnRFReceiptMandatory')"    v-model="Lottable10"></input>
				<i v-if="Lottable10" @click="clearInput('Lottable10')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span></span>
				<input   ref="input_PalleNumber"    copyRef="input_PalleNumber"  @keyup.enter="getFocus('input_LibraryCode')"  @focus="g_focus('input_PalleNumber')"   v-model="input_PalleNumber"></input>
				<i v-if="input_PalleNumber" @click="clearInput('input_PalleNumber')"  class="iconfont icon-cuowu"></i>
			</div>
			<div>
				<span></span>
				<input  ref="input_LibraryCode"  @keyup.enter="submit"   @focus="g_focus('input_LibraryCode')"  v-model="input_LibraryCode"></input>
				<i v-if="input_LibraryCode" @click="clearInput('input_LibraryCode')"  class="iconfont icon-cuowu"></i>
			</div>
			<p class="positionFixed"><span class="g_singleBtn" @click="submit"></span></p>
			<div class="kong"></div>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span></span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'></p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"TakeDelivery",
		data(){
			return{
				input_ReceiptNum:'',//
				input_CustomerNum:'',//
				input_CommodityCode:'',// 1010001000
				input_CustomerCode:'',//
				input_CommodityName:'',//
				input_toBeReceived:'',//
				input_AmountReceived:'',//
				input_num:'',//
				input_unit:'',//
				input_StateOfGoods:'',// 
				input_batchNum:'',//
				input_DateofManufacture:'',//
				input_ExpirationDate:'',//
				Lottable06:"",//06
				Lottable07:"",//07
				Lottable08:"",//08
				Lottable01:"",//01
				input_ReceiptDate:"",//
				Lottable10:"",//10
				input_PalleNumber:'',//
				input_LibraryCode:'STAGE',//
				warningInfo:"",//
				tipsHide:false,//
				msg:"",//
				roterPath:"",//
				inputFocus:"",
				StateOfGoodsData:[],//
			}
		},
		mounted() {

			var that = this;

			this.$refs['input_num'].focus();

			window.QRScanAjax = this.QRScanAjax;
			$("input").attr("maxlength","50");
		}, 
		created(){

			var that = this;

			// 
			if(that.$store.state.Receipt_SubmitData.length){

				var Receipt_SubmitData = that.$store.state.Receipt_SubmitData;
				
				that.input_ReceiptNum = Receipt_SubmitData[0].receiptkey;

				that.input_CustomerNum = Receipt_SubmitData[0].externalreceiptkey2;

				that.input_CustomerCode = Receipt_SubmitData[0].storerkey;
			}

			//   
			if(that.$store.state.RFReceiveSkuData.length){

				var SummaryData = that.$store.state.RFReceiveSkuData[0];

				that.input_CommodityName = SummaryData.Descr; //

				that.input_AmountReceived = SummaryData.qtyReceived; //

				that.input_toBeReceived = SummaryData.qtyRemain; //

				that.input_CommodityCode = that.$route.query.Sku; //

				that.input_unit = SummaryData.Susr2; //

				this.SummaryData = SummaryData;

				this.SummaryData.input_num =1;
			}

			if(that.$store.state.StateOfGoodsData.length){

				that.StateOfGoodsData = that.$store.state.StateOfGoodsData;

			}else{
				var stateUrl = this.HOST + "GoodsStatus.do";	

				that.httpRequest(stateUrl,{Code:"",Descr:""},function(res){

					that.StateOfGoodsData = res.data.data;

					that.$store.state.StateOfGoodsData = res.data.data;

					that.input_StateOfGoods = res.data.data[0].Code;

				})
			}
		},
		methods:{
			// iOS/android
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;
				
				// 
				if(inputName == "input_num") {

					this.getFocus('input_batchNum');

				// 
				}else if(inputName == "input_PalleNumber") {

					this.getFocus('input_LibraryCode');

				// 
				}else if(inputName == "input_LibraryCode") {

					this.submit();
				}
			},
			// 
			ToReceipt:function(){

				var that = this;

				this.$router.push({
					name:"ScanCommodityCode",
					query:{
						input_CommodityCode:that.input_CommodityCode,//
					}
				})
			},
			onblur:function(index,attributeName){
				if(index){

					$("input[requireds]").each(function(i){

						if($("input[requireds]").eq(i).attr("requireds") == attributeName){

							if($("input[requireds]").eq(i).val()){

								$("input[requireds]").eq(i).css("border-color","#bbb");

							}else{
								$("input[requireds]").eq(i).css("border-color","red");
							}
						}
					})
				}
			},
			//  
			nextFocus:function(thisName){

				var that = this;

				console.log(that.SummaryData.Lottable03OnRFReceiptMandatory)
				console.log(that.SummaryData.Lottable04OnRFReceiptMandatory)

				var notFocus = true;
				// var notRequired = true;

				$("input[requireds]").each(function(i){

					if(parseInt(that.SummaryData[$("input[requireds]").eq(i).attr("requireds")])){

						console.log("")
						console.log(i)
						console.log(parseInt(that.SummaryData[$("input[requireds]").eq(i).attr("requireds")]))

						if(thisName == $("input[requireds]").eq(i).attr("requireds")){

							// console.log("")

							if(parseInt(that.SummaryData[$("input[requireds]").eq(i+1).attr("requireds")])){

								that.$refs[$("input[requireds]").eq(i+1).attr("copyRef")].focus();

								notFocus = false;

								return;

							}

						}  
						// notRequired = false;
					}
					// else{
					// 	that.$refs[$("input[requireds]").eq(i+1).attr("copyRef")].focus();
					// }
				})

				if(notFocus){
					that.$refs["input_PalleNumber"].focus();
				}

				// if(notRequired){

				// 	that.$refs[$("input[requireds]").eq(i+1).attr("copyRef")].focus();
				// }

			},
			//  
			submit:function(){

				var that = this;

				var banSubmit = false;

				$("input[requireds]").each(function(i){

					if(!$("input[requireds]").eq(i).val()){

						$(this).css("border-color","red");

						banSubmit = true;

					}else{

						$(this).css("border-color","#bbb");
					}
				})

				//  return
				if(banSubmit){
					that.tipsShow("input_LibraryCode");

					that.msg = "";

					$("input[requireds]").each(function(i){
						console.log(i)

						console.log($("input[requireds]").eq(i).css("border-color"))

						if($("input[requireds]").eq(i).css("border-color") == "rgb(255, 0, 0)"){

							that.inputFocus =  $("input[requireds]").eq(i).attr("requireds");

							return;
						}
					})

					return;
				}

				// 
				for(var j = 0;j < that.StateOfGoodsData.length ; j++){

					if(that.$refs['ref_StateOfGoods'].value == that.StateOfGoodsData[j].Descr){

						that.input_StateOfGoods = that.StateOfGoodsData[j].Code;
					}
				}

				// 
				if(that.input_DateofManufacture.length){

					that.input_DateofManufacture = that.ValidationTimeFormat(that.input_DateofManufacture,"input_DateofManufacture");

					if(!that.input_DateofManufacture){

						that.getFocus("input_DateofManufacture");
						that.input_DateofManufacture = "";
						return;
					}
				}

				// 
				if(that.input_ExpirationDate.length){

					that.input_ExpirationDate = that.ValidationTimeFormat(that.input_ExpirationDate,"input_ExpirationDate");

					if(!that.input_ExpirationDate){

						that.getFocus("input_ExpirationDate");
						that.input_ExpirationDate = "";
						return
					}
				}

				// 
				if(that.input_ReceiptDate.length){
					that.input_ReceiptDate = that.ValidationTimeFormat(that.input_ReceiptDate,"input_ReceiptDate");

					if(!that.input_ReceiptDate){

						that.getFocus("input_ReceiptDate");
						that.input_ReceiptDate = "";
						return
					}
				}
				
				var RFReceiveUrl = this.HOST + "RFReceive.do";

				var r_postData = {
					ReceiptKey : this.input_ReceiptNum,
					StorerKey : this.input_CustomerCode,
					Sku : this.input_CommodityCode,
					Qty : this.input_num,
					Lottable03 : this.input_batchNum,
					Lottable02 : this.input_StateOfGoods,
					Lottable04 : this.input_DateofManufacture,
					Lottable05 : this.input_ExpirationDate,
					PID : this.input_PalleNumber,
					Loc : this.input_LibraryCode,
				}


				this.httpRequest(RFReceiveUrl,r_postData,function(res){

					that.$router.push({
						name:"ScanCommodityCode",
						query:{
						}
					})
				},)
			},
			//
			ValidationTimeFormat:function(time,focusName){

				var that = this;

				var year = new Date().getFullYear() + '';

				if(time.length==4 || time.length==6 || time.length==8 || time.length==10 ){

					if(time.length==10){

						if(time[4]=="-" && time[7]=="-"){

							return time;

						}else{

							that.tipsShow("input_LibraryCode");

							that.msg = " ; 20180524 , 2018-05-24";

							that.inputFocus = focusName;

							return;
						}
					}else if(time.length==4){

						return year + "-" + time.substring(0,2) + "-" + time.substring(2,4);

					}else if(time.length==6 ){

						return year.substring(0,2) +  time.substring(0,2) + "-" + time.substring(2,4) + "-" + time.substring(4,6);

					}else if(time.length==8 ){

						return  time.substring(0,4) + "-" + time.substring(4,6) + "-" + time.substring(6,8);

					}

				}else{

					that.tipsShow("input_LibraryCode");

					that.msg = " ; 20180524 , 2018-05-24";

					that.inputFocus = focusName;

					return;
				}
			}
		}
	}
</script>
<style lang="less" scoped>
