			.container{
				padding-top: 10/50rem;
				input{
					font-size: 25/50rem;
				}
				&>div{
					height: 65/50rem;
					&>div{
						span{
							width: 150/50rem;
							margin-right: 0;
						}
						input{
							width:212/50rem;
							height:55/50rem;
						}
					}
					&.select{
						height: 70/50rem;
					}
					select{
						width:570/50rem;
						height:60/50rem;
						font-size: 25/50rem;
					}
					&>span{
						width: 160/50rem;
